export default class Transform {
  constructor() {}

  objKeysToLower(obj, oWraper = {}) {
    return new Promise(async (resolve, reject) => {
      try {
        for (let prop in obj) {
          if (prop !== '' && obj[prop]) {
            if (obj[prop] instanceof Object && !obj[prop].hasOwnProperty('length')) {
              oWraper[prop.toLowerCase()] = await this.objKeysToLower(obj[prop], {});
            } else if (obj[prop] instanceof Array) {
              oWraper[prop.toLowerCase()] = await this.arrKeysToLower(obj[prop], []);
            } else {
              oWraper[prop.toLowerCase()] = obj[prop];
            }
          }
        }
        resolve(oWraper);
      } catch (err) {
        reject(err)
      }
    });
  }

  arrKeysToLower(arr, aWraper = []) {
    return new Promise(async (resolve, reject) => {
      try {
        const len = arr.length;
        for (let i = 0; i < len; i++) {
          if (arr[i] instanceof Object && !arr[i].hasOwnProperty('length')) {
            const o = await this.objKeysToLower(arr[i], {});
            aWraper.push(o);
          } else  if (arr[i] instanceof Array) {
            const o = await this.arrKeysToLower(arr[i], []);
            aWraper.push(o);
          } else {
            aWraper.push(arr[i]);
          }
        }
        resolve(aWraper);
      } catch (err) {
        reject(err)
      }
    });
  }

  objKeysToUpper(obj, oWraper = {}) {
    return new Promise(async (resolve, reject) => {
      try {
        for (let prop in obj) {
          if (prop !== '' && obj[prop]) {
            if (obj[prop] instanceof Object && !obj[prop].hasOwnProperty('length')) {
              oWraper[prop.toUpperCase()] = await this.objKeysToUpper(obj[prop], {});
            } else if (obj[prop] instanceof Array) {
              oWraper[prop.toUpperCase()] = await this.arrKeysToUpper(obj[prop], []);
            } else {
              oWraper[prop.toUpperCase()] = obj[prop];
            }
          }
        }
        resolve(oWraper);
      } catch (err) {
        reject(err)
      }
    });
  }

  arrKeysToUpper(arr, aWraper = []) {
    return new Promise(async (resolve, reject) => {
      try {
        const len = arr.length;
        for (let i = 0; i < len; i++) {
          if (arr[i] instanceof Object && !arr[i].hasOwnProperty('length')) {
            const o = await this.objKeysToUpper(arr[i], {});
            aWraper.push(o);
          } else  if (arr[i] instanceof Array) {
            const o = await this.arrKeysToUpper(arr[i], []);
            aWraper.push(o);
          } else {
            aWraper.push(arr[i]);
          }
        }
        resolve(aWraper);
      } catch (err) {
        reject(err)
      }
    });
  }

}
